<template>
  <div class="app-container">
    <div class="home">
      <h2>视频前端压缩</h2>
      <video id="video" controls :src="vedioSrc"></video><br />
      <!-- <input type="file" id="upload" @change="upload" /> -->
      <input type="file" id="upload2" @change="initFfmpeg" />
      <p id="text">{{ msg }}</p>
      进度:<el-progress type="circle" :percentage="percentage"></el-progress>
    </div>
  </div>
</template>
<script>
import { createFFmpeg, fetchFile } from "@ffmpeg/ffmpeg";
export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      msg: null,
      percentage:0,
      vedioSrc:null
    };
  },
  computed: {},
  watch: {},
  mounted() {},
  methods: {
    //初始化
    initFfmpeg(e) {

      const ffmpeg = createFFmpeg({
        corePath: "ffmpeg-core.js",
        log: true,
      });
      //设置进度条
      ffmpeg.setProgress(({ ratio }) => {
        console.log(ratio);
        this.percentage = Math.floor(ratio * 100);
      });
      var file = e.target.files[0];
      //开始压缩
      const transcode = async (file) => {
        const { name } = file;
        this.message = "Loading ffmpeg-core.js";
        await ffmpeg.load();
        ffmpeg.FS("writeFile", name, await fetchFile(file));
        this.message = "Start transcoding";
        // '-b','2000000'  值越小  压缩率越大
        await ffmpeg.run("-i", name, "-b", "70000", "output.mp4");
        this.message = "压缩完成";
        const data = ffmpeg.FS("readFile", "output.mp4");
        this.fileBytes = data.byteLength;
        //把压缩后的视频进行回显
        this.vedioSrc = URL.createObjectURL(new Blob([data.buffer], { type: "video/mp4" }));
      };
      transcode(file);
    },
    async upload(e) {
      var _this = this;
      const ffmpeg = createFFmpeg({
        log: true,
        corePath: "ffmpeg-core.js",
        progress: ({ ratio }) => {
          _this.msg = `完成率: ${(ratio * 100.0).toFixed(2)}%`;
        },
      });
      var { name } = e.target.files[0];

      this.msg = "正在加载 ffmpeg-core.js";
      await ffmpeg.load();
      this.msg = "开始压缩";
      ffmpeg.FS("writeFile", name, await fetchFile(e.target.files[0]));
      await ffmpeg.run("-i", name, "-b", "2000000", "put.mp4");
      this.msg = "压缩完成";
      const data = ffmpeg.FS("readFile", "put.mp4");
      console.log(data);
      // const video = document.getElementById('video');
      // video.src = URL.createObjectURL(new Blob([data.buffer], {
      //   type: 'video/mp4'
      // }));
    },
  },
};
</script>
<style lang="" scoped></style>
