<template>
  <div class="app-container">
    <div class="table_select_tag">
      <el-tag v-for="item in multipleSelection" :key="item.date" effect="dark" closable @close="tableSelectTagClose(item)">
        {{ item.name }}
      </el-tag>
    </div>
    <el-table
      ref="multipleTable"
      :data="dataTableData"
      tooltip-effect="dark"
      @selection-change="handleSelectionChange"
      align="center"
      row-key="id"
      :height="660"
      style="width: 100%"
    >
      <el-table-column type="selection" width="55" align="center" :reserve-selection="true"> </el-table-column>
      <el-table-column prop="id" label="ID" width="120" align="center"> </el-table-column>
      <el-table-column label="日期" width="120" align="center">
        <template slot-scope="scope" align="center">{{ scope.row.date }}</template>
      </el-table-column>
      <el-table-column prop="name" label="姓名" width="120" align="center"> </el-table-column>
      <el-table-column prop="address" label="地址" show-overflow-tooltip align="center" min-width="200"> </el-table-column>
      <el-table-column prop="address" label="地址" show-overflow-tooltip align="center" min-width="200"> </el-table-column>
      <el-table-column prop="address" label="地址" show-overflow-tooltip align="center" min-width="200"> </el-table-column>
      <el-table-column prop="address" label="地址" show-overflow-tooltip align="center" min-width="200"> </el-table-column>
      <el-table-column prop="address" label="地址" show-overflow-tooltip align="center" min-width="200"> </el-table-column>
      <el-table-column prop="address" label="地址" show-overflow-tooltip align="center" min-width="200"> </el-table-column>
      <el-table-column prop="address" label="地址" show-overflow-tooltip align="center" min-width="200"> </el-table-column>
      <el-table-column prop="address" label="地址" show-overflow-tooltip align="center" min-width="200"> </el-table-column>
      <el-table-column prop="address" label="地址" show-overflow-tooltip align="center" min-width="200"> </el-table-column>
      <el-table-column prop="address" label="地址" show-overflow-tooltip align="center" min-width="200"> </el-table-column>
    </el-table>
    <el-pagination :page-size="pageSize" layout="prev, pager, next" :total="total" :current-page="1" @current-change="handleCurrentChange"> </el-pagination>
  </div>
</template>
<script>
import { tableData } from "../../utils/tableData";
export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      dataTableData: [],
      total: 0,
      pageSize: 20,
      multipleSelection: [],
    };
  },
  computed: {},
  watch: {},
  mounted() {
    this.dataTableData = tableData.slice(0, this.pageSize);
    this.total = tableData.length;
    var _this = this;
    var rows = _this.multipleSelection;
    var mapRows = rows.map((item) => item.id);
    this.$nextTick(function () {
      this.dataTableData.forEach((element) => {
        if (mapRows.includes(element.id)) {
          this.$refs.multipleTable.toggleRowSelection(element, true);
        }
      });
    });
  },
  methods: {
    handleCurrentChange(val) {
      console.log(val);
      this.dataTableData = tableData.slice((val - 1) * this.pageSize, val * this.pageSize);
      var mapRows = this.multipleSelection.map((item) => item.id);
      this.$nextTick(function () {
        this.dataTableData.forEach((element) => {
          if (mapRows.includes(element.id)) {
            this.$refs.multipleTable.toggleRowSelection(element, true);
          }else{
            this.$refs.multipleTable.toggleRowSelection(element, false);
          }
        });
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    tableSelectTagClose(data) {
      var dataArray = [data];
      var mapRows = dataArray.map((item) => item.id);
      console.log(this.multipleSelection)
      var dataId = data.id;
      this.multipleSelection.forEach((item,index)=>{
       console.log(item.id,' index ',index)
        if(item.id == dataId){
          this.multipleSelection.splice(index,1)
        }
      })
      this.$nextTick(function () {
        this.dataTableData.forEach((element) => {
          if (mapRows.includes(element.id)) {
            this.$refs.multipleTable.toggleRowSelection(element, false);
          }else{

          }
        });
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.table_select_tag {
  min-height: 80px;
  border: 1px solid #8c8c8c;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
  .el-tag {
    margin-right: 10px;
    margin-bottom: 10px;
  }
}
</style>
