<template>
  <div class="app-container">
    <el-button type="primary" @click="submitTableList">提交数据</el-button>
    <el-table
      ref="multipleTable"
      :data="dataTableData"
      tooltip-effect="dark"
      @selection-change="handleSelectionChange"
      align="center"
      row-key="id"
      :height="680"
      style="width: 100%;"
    >
      <el-table-column type="selection" width="55" align="center" :reserve-selection="true"> </el-table-column>
      <el-table-column prop="id" label="ID" width="120" align="center"> </el-table-column>
      <el-table-column label="日期" width="300" align="center">
        <template slot-scope="scope" align="center">
          <el-date-picker v-model="scope.row.date" type="date" placeholder="选择日期" value-format="yyyy-MM-dd"> </el-date-picker>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="姓名" width="300" align="center"> </el-table-column>
      <el-table-column label="地址" show-overflow-tooltip align="center" min-width="200">
        <template slot-scope="scope">
          <el-input v-model="scope.row.address"></el-input>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
import { tableData } from "../../utils/tableData";
export default {
  name: "",
  components: {},
  mixins: [],
  props: {},
  data() {
    return {
      dataTableData: [],
      multipleSelection: [],
    };
  },
  computed: {},
  watch: {},
  mounted() {
    this.dataTableData = tableData.slice(0, 20);
  },
  methods: {
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    submitTableList() {
      console.log(JSON.stringify(this.dataTableData));
      console.log(this.dataTableData);
      this.$alert("<p>这是你所提交的表格数据</p></p><br>" + JSON.stringify(this.dataTableData), "数据提交", {
        dangerouslyUseHTMLString: true,
        confirmButtonText: "确定",
      });
    },
  },
};
</script>
<style lang="scss" scoped></style>
